.wrapper
{
	width: 100vw;
	height: 100vh;
	position: relative;
	overflow: hidden;
}
.pos{
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
}
.texto{
	background: url();
	background-size: cover;
	transform: translate3d(0, 30px, 0);
	opacity: 0;
	animation: moveUp 1.8s ease-out;
	animation-fill-mode: forwards;

}
.img1{
	background: url();
	background-size: cover;
	transform: scale3d(1.1,1.1,1.1);
	opacity: 0.2;
	animation: scaler 1s ease-out;
	animation-fill-mode: forwards;
}
.img2{
	background: url();
	background-size: cover;
	transform: scale3d(1.3,1.3,1.3);
	opacity: 0.1;
	animation: scaler 1.2s ease-out;
	animation-fill-mode: forwards;
}
@keyframes moveUP{
	100%{
		transform: translate3d(0,0,0);
		opacity: 1;
	}
}
@keyframes scaler{
	100%{
		transform: translate3d(1,1,1);
		opacity: 1;
	}
}
.body_est{
	margin: 0;
	padding: 0;
	height: 100%;
	background: url();
	background-size: cover;
}
